// import { sortBy } from 'lodash'
import keyMirror from 'keymirror'
import firebase from 'firebase';
import { SETTINGS } from 'constants/Constants';
import { toast } from 'react-toastify';
import { initialize } from 'redux-form'

const TYPE = keyMirror({
  ADD_DELIVERY_MESSAGE: null,
  GET_DELIVERY_MESSAGE: null,
});

const initState = {

};

export const addDeliveryMessage = ({ deliveryMessage }) => {
  return dispatch => {
    const firestore = firebase.firestore();
    firestore.collection(SETTINGS.settings).doc(SETTINGS.deliveryMessage).set({ ...deliveryMessage })
      .then(() => {
        toast.success("Update thành công");
        dispatch({ type: TYPE.ADD_DELIVERY_MESSAGE, payload: { deliveryMessage } })
      })
      .catch(err => { console.log(err) });
  }
}

export const getDeliveryMessage = () => {
  return dispatch => {
    const firestore = firebase.firestore();
    firestore.collection(SETTINGS.settings).doc(SETTINGS.deliveryMessage).get()
      .then(res => {
        const deliveryMessage = res ? res.data() : '';
        dispatch({ type: TYPE.GET_DELIVERY_MESSAGE, payload: { deliveryMessage } })
        dispatch(initialize(SETTINGS.SETTINGS_FORM, { ...deliveryMessage }));
      })
      .catch(err => { console.log(err) });
  }
};

export default (state = initState, action) => {
  const { payload } = action;
  switch (action.type) {
    case TYPE.ADD_DELIVERY_MESSAGE: {
      const deliveryMessage = payload.deliveryMessage;
      return { ...state, ...deliveryMessage };
    }
    case TYPE.GET_DELIVERY_MESSAGE: {
      const deliveryMessage = payload.deliveryMessage;
      return { ...state, ...deliveryMessage };
    }
    default:
      return state;
  }
}
