import firebase from 'firebase'
import { groupBy } from 'lodash'
import { THANG, XEM_TAT_CA, MONTH, YEAR } from '../constants/Constants';
export const GET_DON_HANGS = 'GET_DON_HANGS';
export const GET_DON_HANGS_THONG_KE = 'GET_DON_HANGS_THONG_KE';
export const UPDATE_DON_HANG = 'UPDATE_DON_HANG';
export const GET_CUSTOMERS = 'GET_CUSTOMERS';
export const GET_CUSTOMERS_PHONES = 'GET_CUSTOMERS_PHONES';
export const LOG_IN = "LOG_IN";
export const LOG_OUT = "LOG_OUT";

export const GET_DOANH_THU_CUSTOMERS_THONG_KE = 'GET_DOANH_THU_CUSTOMERS_THONG_KE';
export const GET_DOANH_THU_TIME_THONG_KE = 'GET_DOANH_THU_TIME_THONG_KE';

const initState = {
  donhang: {},
  donhangsThongKe: [],
  doanhThuCustomers: [],
  doanhThuTime: [],
  sanPhams: [],
  customers: {},
  test: 'test',
}

export const getDonHangs = ({ type, year, toMonth, fromMonth }) => {
  return dispatch => {
    if (type === THANG) {
      firebase.database().ref(`donhangs/${year}`).orderByChild('month')
        .startAt(toMonth).endAt(fromMonth).once('value').then(snapshot => {
          const donhangs = snapshot.val();
          dispatch({
            type: GET_DON_HANGS,
            payload: { donhangs }
          })
        })
    } else if (type === XEM_TAT_CA) {
      const url = `/donhangs/${year}`
      firebase.database().ref(url).once('value').then(snapshot => {
        const donhangs = snapshot.val();
        dispatch({
          type: GET_DON_HANGS,
          payload: { donhangs }
        })
      });
    }
  }
}

export const getDonHangsThongKe = ({ year, month }) => {
  return dispatch => {
    firebase.database().ref(`donhangs/${year}`).orderByChild('month')
      .equalTo(month).once('value').then(snapshot => {
        const donhangsThongKe = snapshot.val();
        dispatch({
          type: GET_DON_HANGS_THONG_KE,
          payload: { donhangsThongKe }
        })
      })
  }
}

export const getDoanhThuCustomersThongKe = ({ type, year, toMonth, fromMonth }) => {
  return dispatch => {
    const donhangsYearUrl = `/donhangs/${year}`
    const donhangsYear = firebase.database().ref(donhangsYearUrl);
    if (type === MONTH) {
      donhangsYear.orderByChild('month').startAt(toMonth).endAt(fromMonth).once('value')
        .then(snapshot => {
          dispatchDoanhThuCustomersThongKe({ dispatch, snapshot });
        })
    } else if (type === YEAR) {
      donhangsYear.once('value')
        .then(snapshot => {
          dispatchDoanhThuCustomersThongKe({ dispatch, snapshot });
        });
    }
  }
}

export const getDoanhThuTimeThongKe = ({ year }) => {
  return dispatch => {
    const donhangsYearUrl = `/donhangs/${year}`
    const donhangsYear = firebase.database().ref(donhangsYearUrl);
    donhangsYear.once('value')
      .then(snapshot => {
        dispatchDoanhThuTimeThongKe({ dispatch, snapshot });
      });
  }
}

const dispatchDoanhThuCustomersThongKe = ({ dispatch, snapshot }) => {
  const donhangs = snapshot.val();
  const doanhThuCustomers = groupBy(donhangs, (donhang) => {
    return donhang.khachhang.congTy;
  })
  dispatch({
    type: GET_DOANH_THU_CUSTOMERS_THONG_KE,
    payload: { doanhThuCustomers }
  })
}

const dispatchDoanhThuTimeThongKe = ({ dispatch, snapshot }) => {
  const donhangs = snapshot.val();
  const doanhThuTime = groupBy(donhangs, (donhang) => {
    return donhang.month;
  })
  dispatch({
    type: GET_DOANH_THU_TIME_THONG_KE,
    payload: { doanhThuTime }
  })
}

export const getCustomers = () => {
  return dispatch => {
    const url = '/customers/congTy'
    firebase.database().ref(url).once('value').then(snapshot => {
      const customers = snapshot.val();
      dispatch({
        type: GET_CUSTOMERS,
        payload: { customers }
      })
    });
  }
}

export const getCustomersPhones = () => {
  return dispatch => {
    const url = '/customers/soDienThoai'
    firebase.database().ref(url).once('value').then(snapshot => {
      const customersPhones = snapshot.val();
      dispatch({
        type: GET_CUSTOMERS_PHONES,
        payload: { customersPhones }
      })
    });
  }
}

export const updateDonHang = ({ newDonHang }) => {
  return dispatch => {
    const updatedOrdersURL = `/updatedDonHangs/${newDonHang.maDonHang}`;
    const donhangsURL = `/donhangs/${newDonHang.year}/${newDonHang.maDonHang}`
    try {
      firebase.database().ref(updatedOrdersURL).push(newDonHang)
      firebase.database().ref(donhangsURL).set(newDonHang)
    } catch (error) {

    }
  }
}

export default (state = initState, action) => {
  const { payload } = action;
  switch (action.type) {
    case GET_DON_HANGS: {
      const donhangs = payload.donhangs;
      return { ...state, donhangs };
    }
    case GET_CUSTOMERS: {
      const customers = payload.customers;
      return { ...state, customers };
    }
    case GET_DON_HANGS_THONG_KE: {
      const donhangsThongKe = payload.donhangsThongKe;
      return { ...state, donhangsThongKe };
    }
    case GET_CUSTOMERS_PHONES: {
      const customersPhones = payload.customersPhones;
      return { ...state, customersPhones };
    }
    case GET_DOANH_THU_CUSTOMERS_THONG_KE: {
      const doanhThuCustomers = payload.doanhThuCustomers;
      return { ...state, doanhThuCustomers };
    }
    case GET_DOANH_THU_TIME_THONG_KE: {
      const doanhThuTime = payload.doanhThuTime;
      return { ...state, doanhThuTime };
    }
    default:
      return state;
  }
}
